export function getEvolutionIds(id: number) {
    let allIds: Set<number>;
    allIds = new Set();
    allIds.add(id);
    for (let i = 0; i < 5; i++) {
        allIds.forEach((id: number) => {
            originData.filter(element => {
                return element.id1 === id || element.id2 === id;
            }).forEach((element) => {
                allIds.add(element.id1);
                allIds.add(element.id2);
            });
        });
    }

    let tempIds: Array<number>;
    tempIds = [];
    allIds.forEach((id: number) => {
        tempIds.push(id);
    });
    return tempIds;
}

export function hasAnyEvo(id: number, ownedCards: Array<number>) {
    return getEvolutionIds(id).some(item => ownedCards.includes(item));
}


export function getWikiImage(id: number) {
    const base: string = "https://static.wikia.nocookie.net/towerofsaviors/images/";
    const filename: string = id + 'i.png';
    const result: string = MD5(filename);
    result.replace(/\(/g,'%28').replace(/\)/,'%29');
    return base + result.substring(0,1) + '/' + result.substring(0,2) + '/' + filename;
}

